import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import superagent from 'superagent'

export const createPoll = createAsyncThunk('governance/createPoll', async payload => {
  await superagent.put("http://localhost:8090/governance/createPoll")
    .send(payload)

  return payload
})

