import { useState } from 'react';
import ReactDOM from 'react-dom';
import ergoImg from '../../images/only-app/Ergo.png'
import imgG16 from "../../images/only-app/Group16.png"
import Modal from './Modal';

const AmountModal = props => {
  const data = props.data
  const tabStates = {
    add: "add",
    subtract: "subtract"
  }
  const [tab, setTab] = useState(tabStates.add)
  const currentTab = tab === "add" ? data.addTab : data.subtractTab

  const [value, setValue] = useState('')
  const isValueValid = parseInt(value) && currentTab.validator(parseInt(value))
  const onMaxClick = () => setValue(currentTab.maxValue)

  const onSubmit = () => currentTab.onSubmit(parseInt(value))
  const onClose = () => {
    setTab(tabStates.add)
    props.onClose()
  }
  
  const assetAPY = data.rates.asset
  const el = (
    <Modal>
      <div className="modal-content">
        <div className="modal-header text-center">
          <h3 className="modal-title marketers-pupup-header" id="exampleModalLabel"><img src={data.asset.image} /><strong>{data.asset.title}</strong></h3>
          <button onClick={onClose} type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
        </div>
        <div className="modal-body">
          <div className="row value-area align-items-center">
            <div className="col-2" />
            <div className="col-8 text-center">
              <input type="text" aria-label="amount-input" value={value} onChange={e => setValue(e.target.value)} placeholder="0" className={"big-count form-control" + (isValueValid ? "" : " text-danger")} style={{ textAlign: "center", border: "0px" }} />
            </div>
            <div className="col-2" onClick={onMaxClick} style={{ cursor: "pointer" }}>MAX</div>
          </div>
          <ul className="nav justify-content-between minus-pluss" id="supplyTab" role="tablist">
            <li className="nav-item text-center">
              <button onClick={() => setTab(tabStates.add)} className={tab === tabStates.add ? "active" : ""} id="supply-first-tab-tab" data-bs-toggle="tab" data-bs-target="#supply-first-tab-t" role="tab" aria-controls="supply-first-tab-t" aria-selected="true">
                <svg xmlns="http://www.w3.org/2000/svg" width={20} height={20} viewBox="0 0 20 20">
                  <g id="e-add" transform="translate(-1 -1)">
                    <g id="Group_125" data-name="Group 125" transform="translate(1 1)">
                      <path id="Path_244" data-name="Path 244" d="M20.091,10.091H11.909V1.909a.909.909,0,1,0-1.818,0v8.182H1.909a.909.909,0,1,0,0,1.818h8.182v8.182a.909.909,0,1,0,1.818,0V11.909h8.182a.909.909,0,1,0,0-1.818Z" transform="translate(-1 -1)" fill="#21bba6" />
                    </g>
                  </g>
                </svg>
                {data.addTab.title}
              </button>
            </li>
            <li className="nav-item text-center">
              <button onClick={() => setTab(tabStates.subtract)} className={tab === tabStates.subtract ? "active" : ""} id="supply-second-tab-tab" data-bs-toggle="tab" data-bs-target="#supply-second-tab-t" role="tab" aria-controls="supply-second-tab-t" aria-selected="false">
                <svg xmlns="http://www.w3.org/2000/svg" width={20} height="1.818" viewBox="0 0 20 1.818">
                  <g id="e-add" transform="translate(-1 -10)" opacity="0.6">
                    <g id="Group_125" data-name="Group 125" transform="translate(1 11)">
                      <path id="Path_244" data-name="Path 244" d="M20.091,11H1.909a.909.909,0,0,0,0,1.818H20.091a.909.909,0,1,0,0-1.818Z" transform="translate(-1 -12)" fill="#494763" />
                    </g>
                  </g>
                </svg>
                {data.subtractTab.title}
              </button>
            </li>
          </ul>
          <div className="tab-content" id="supplyTabContent">
            <div className="tab-pane supply-tab fade show active" id="supply-first-tab-t" role="tabpanel" aria-labelledby="supply-first-tab">
              <div className="row modal-table">
                <div className="col-12">
                  <h4>
                    {data.rateTitle} Rates
                    <svg xmlns="http://www.w3.org/2000/svg" width="9.2" height="8.967" viewBox="0 0 9.2 8.967">
                      <path id="Path_238" data-name="Path 238" d="M3.912,38.251l.456-.456a.491.491,0,0,1,.7,0l3.992,3.99a.491.491,0,0,1,0,.7L5.064,46.472a.491.491,0,0,1-.7,0l-.456-.456a.493.493,0,0,1,.008-.7l2.474-2.357H.493A.492.492,0,0,1,0,42.462V41.8a.492.492,0,0,1,.493-.493h5.9L3.92,38.955A.49.49,0,0,1,3.912,38.251Z" transform="translate(0 -37.65)" fill="#21bba6" />
                    </svg>
                  </h4>
                </div>
              </div>
              <div className="row modal-table bordered-row">
                <div className="col-6 name">
                  <img src={data.asset.image} />{data.asset.title}
                </div>
                <div className="col-6 text-end rate">
                  {assetAPY.toFixed(2)}%
                </div>
              </div>
              <div className="row modal-table bordered-row">
                <div className="col-6 name">
                  <img src={imgG16} />Distribution APY
                </div>
                <div className="col-6 text-end rate">
                  {assetAPY.toFixed(2)}%
                </div>
              </div>
              <div className="row modal-table">
                <div className="col-12">
                  <h4>
                    Borrow Limit
                  </h4>
                </div>
              </div>
              <div className="row modal-table bordered-row">
                <div className="col-6 name">
                  Borrow Limit
                </div>
                <div className="col-6 text-end rate">
                  ${data.borrowLimitDollars.toFixed(2)}
                </div>
              </div>
              <div className="row modal-table bordered-row">
                <div className="col-6 name">
                  Borrow Limit Used
                </div>
                <div className="col-6 text-end rate">
                  {data.borrowPercentage.toFixed(2)}%
                </div>
              </div>
              <div className="row modal-table">
                <div className="col-12">
                  <div className="progress" style={{ height: '10px' }}>
                    <div className="progress-bar" role="progressbar" style={{ width: `${Math.floor(data.borrowPercentage * 100)}%` }} aria-valuenow={data.borrowPercentage * 100} aria-valuemin={0} aria-valuemax={100} />
                  </div>
                </div>
              </div>
              <div className="text-center">
                {currentTab.available ?
                  <button aria-label="modal-submit-button" className="btn modal-btn"
                    onClick={onSubmit}
                    disabled={!isValueValid}>
                    Submit
                  </button>
                  : <button className="btn modal-btn" disabled>
                    {currentTab.unavailableMessage}
                  </button>
                }
              </div>
              <div className="row modal-table">
                <div className="col-6 name">
                  {currentTab.infoTitle}
                </div>
                <div className="col-6 text-end rate">
                  {currentTab.infoValue}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Modal>
  )
  
  return el
}

export default AmountModal