import ReactDOM from 'react-dom';

const Modal = ({children}) => {
  // console.dir(document.getElementById("modal-content"))
  return ReactDOM.createPortal(
    <div className="modal fade show" tabIndex="-1" style={{ "display": "block", "paddingLeft": "0px" }} aria-modal="true" role="dialog">
      <div className="modal-dialog modal-dialog-centered">
        {children}
      </div>
    </div>
    , document.getElementById("modal-content") || document.createElement('div')
  )
}

export default Modal;