import React from "react"
import Site from './layout/Site'
import Governance from './layout/Governance'
import NewPoll from "./layout/NewPoll"
import Whitelist from "./layout/Whitelist"
import PollDetails from "./layout/PollDetails"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom"


const App = () => {
  return (
    <div id="site-container" >
      <Router basename="/lq-app-demo" >
        <Switch>
          <Route exact path="/">
            <Site />
          </Route>
          <Route path="/vote">
            <Governance />
          </Route>
          <Route path="/new-poll">
            <NewPoll />
          </Route>
          <Route path="/poll-details">
            <PollDetails />
          </Route>
          <Route path="/whitelist">
            <Whitelist />
          </Route>
        </Switch>

        

        <div id="modal-content" />
      </Router>
    </div>
  )
}

export default App;