import { createSlice } from "@reduxjs/toolkit";

export const marketSlice = createSlice({
  name: "market",
  initialState: {
    markets: [
      {
        ID: "ADA",
        asset: "ADA",
        title: "Cardano",
        supplyApy: 1.12,
        borrowApy: 4.23,
        lqApy: 1.41,
        borrowLqApy: 4.01,
        usdExchange: 1.75127,
        liquidity: 42500949
      },
      {
        ID: "ERG",
        asset: "ERG",
        title: "Ergo",
        supplyApy: 2.34,
        borrowApy: 6.22,
        lqApy: 1.41,
        borrowLqApy: 5.01,
        usdExchange: 7.75127,
        liquidity: 12000123
      }
    ]
  }
})

// -- selectors

const selectMarkets = state => state.market.markets
export const selectMarketIds = state => selectMarkets(state).map(m => m.ID)
export const selectMarketById = (state,id) => selectMarkets(state).find(m => m.ID === id)
export const selectUsdExchangeForId = (state, id) => selectMarketById(state,id).usdExchange