async function main() {
  // if (window.location.pathname === '/lq-app-demo') {
  //   window.location.pathname = '/lq-app-demo/'
  //   return
  // }
  const { worker } = require('./mocks/browser')
  await worker.start({
    serviceWorker: {
      url: '/lq-app-demo/mockServiceWorker.js',
    },
    // onUnhandledRequest: 'warn'
    onUnhandledRequest: 'bypass'
  })
  console.log("Started mock sw")
  worker.printHandlers()
  ReactDOM.render(
    <React.StrictMode>
      <Provider store={store}>
        <App />
      </Provider>
    </React.StrictMode>,
    document.getElementById('root')
  );
}
main()

